import i18n from 'i18next';
import {initReactI18next} from 'react-i18next';
import {ko , en, cn} from './locales';

//dummy
const resources = {

  en : {
    translation:{
      welcome : 'welcome',
      login : 'login',
    },
  },
  ko : {
    translation:{
      welcome :'.',
      login : '',
    }
  }
}

i18n.use(initReactI18next).init({
  resources : {ko,en,cn},
  lng : 'ko',
  interpolation :{
    escapeValue : false,
  }
});

export default i18n;