import React from 'react'
import styled from 'styled-components';
import { DARKGREEN } from '../../styles/common';

function Condition({data}) {
  return (
    <Wrapper>
      <Title> </Title>
      <CondBox>
        <Label><div></div><span>{data.salary}</span></Label>
        <Label><div> </div><span>{data.employmentPeriod}</span></Label>
        <Label><div></div><span>{data.sector}</span></Label>
        <Label><div> </div><span>{data.workDays}</span></Label>
        <Label><div></div><span>{data.foodProvided ? " O" : " X"} / {data.accommodationProvided? " O": " X"}</span></Label>
        <Label><div> </div><span>{data.workHours}</span></Label>
      </CondBox>
    </Wrapper>
  )
}

export default Condition;

const Wrapper = styled.div`
  //width : 800px;
  height : 450px;

  border-radius : 5px;
  margin-top : 20px;

  position : relative;
  //background-color : #eee;
`;

const Title = styled.div`
  font-size : 48px;
  color : ${DARKGREEN};
`;

const CondBox = styled.div`
  height : 347px;
  background-color : #EEF4EB;
  border-radius : 15px;
  padding : 40px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(3, 1fr);
  //gap: 20px;
`;

const Label = styled.div`
  //background-color: #ccc;
  padding: 20px;
  display : flex;
  text-align: center;
  align-items : center;

  & > div{
    font-size : 38px;
    font-weight : 600;
    margin-right : 70px;
    color : ${DARKGREEN}
  }
  & > span {
    font-size: 29px;
  }
`;
