import React, { startTransition, useEffect, useState } from 'react';
import "../../styles/JobPostingForm.css"
import Selectors from './Selectors';
import styled from 'styled-components';

const SECTORS = ["","","",""];
const PERIODS = ['1 ','3 ','6 ','1 ','3 ']
const TIMES_HOUR = Array.from({ length: 24 }, (_, index) => index.toString().padStart(2, '0'));
const TIMES_MIN = ["10","20","30","40","50"];

const Wrapper = styled.div`
    position :relative;

`;
const BUTTON = styled.button`
    position :absolute;
    bottom : 30px;
    right : 30px;


    font-size : 35px;
    font-weight : 500;
    color : #fff;
    
    

    //border : 1px solid #372417;
`;



const JobPostingForm = () => {
    const [formData, setFormData] = useState({
        title: '',
        companyName: '',
        companyName_en: '',
        phoneNumber: '',
        profileImage: null,
        sector: '',
        city: '',
        companyImages: null,
        detailAddress: '',
        detailAddress_en: '',
        employmentPeriod: '',
        workDays: '',
        workHours: '',
        details: null,
        salary: '',
        isAccommodationProvided: false,
        isFoodProvided: false,
    });

    const dropChange = (key, value) => {
        setFormData((prev) => ({
            ...prev,
            [key]: value,
        }));

    }

    const generateEmploymentPeriod = () => {
        let startTime = `${clickedStartHour} ${clickedStartMin}`;
        let endTime = `${clickedStartHour2} ${clickedStartMin2}`;

        setFormData(prev => ({
            ...prev,
            employmentPeriod: `${startTime} ~ ${endTime}`
        }))

    }

    useEffect(() => console.log(formData), [formData]);


    const handleChange = (event) => {
        const { name, value, type, files, checked } = event.target;

        setFormData((prev) => ({
            ...prev,
            [name]: type === 'checkbox' ? checked : type === 'file' ? files : value,
        }));

        console.log(formData)
    };

    const handleSubmit = async (event) => {
        event.preventDefault();
        const submitData = new FormData();

        setTimeout(()=>{
            generateEmploymentPeriod(); 
        },500)

        Object.keys(formData).forEach((key) => {
            //console.log(key, key.includes("Image") , typeof key)
            //console.log(key , formData[key]);
            if (key.includes("Image") || key === "details") {
                for (let i = 0; i < formData[key].length; i++) {
                    submitData.append(key, formData[key][i]);
                }
            }
            else {
                submitData.append(key, formData[key]);
            }
        });

        try {
            const response = await fetch('https://effd-58-224-72-100.ngrok-free.app/api/create-job-posting', {
                method: 'POST',
                body: submitData,
            });

            if (response.ok) {
                const result = await response.json();
                console.log(result);
                alert('    .');
            } else {
                console.log(response);
                alert('   .');
            }
        } catch (error) {
            console.log('   .', error);
        }
    };

    const handlePrintFormData = () => {
        for (const [key, value] of Object.entries(formData)) {
            console.log(`${key}: ${value}`);
        }
    };

    const [selectSector, setSelectSector] = useState(false);
    const [selectFood, setSelectFood] = useState(false);
    const [selectPeriod, setSelectPeriod] = useState(false);
    const [selectStartHour, setSelectStartHour] = useState(false); {/*  */ }
    const [selectStartHour2, setSelectStartHour2] = useState(false);
    const [selectStartMin, setSelectStartMin] = useState(false);
    const [selectStartMin2, setSelectStartMin2] = useState(false);
    const [selectRegion, setSelectRegion] = useState(false);
    const [clickedSector, setClickedSector] = useState({});
    const [clickedCity, setClickedCity] = useState({});
    const [clickedEmPeriod, setClickedEmPeriod] = useState({});
    const [clickedStartHour, setClickedStartHour] = useState({});
    const [clickedStartHour2, setClickedStartHour2] = useState({});
    const [clickedStartMin, setClickedStartMin] = useState({});
    const [clickedStartMin2, setClickedStartMin2] = useState({});

    const createStartHour = (hour) => {
        setClickedStartHour(hour);
        console.log(clickedStartHour)

    }

    const createStartMin = (min) => {
        setClickedStartMin(min);
        console.log(clickedStartMin)

    }

    const createEndHour = (hour) => {
        setClickedStartHour2(hour);
        console.log(clickedStartHour2);
    }

    const createEndMin = (min) => {
        setClickedStartMin2(min);
        console.log(clickedStartMin2)
    }

    const handleFood = () => {
        setSelectFood(!selectFood);
    }

    const showFood = () => {
        return (
            selectFood ?
                <div>
                    <div className="clicked_accomo">
                        <div className="accomo_body">

                            <ul className="accomo_content">
                                <li className='custom-checkbox'>
                                    <input

                                        type="checkbox"
                                        name="isAccommodationProvided"
                                        onChange={handleChange}
                                        checked={formData.isAccommodationProvided}
                                    />
                                    <label className='text_color2'> </label>
                                    <br /><br />

                                    <input
                                        className='input_style'
                                        type="checkbox"
                                        name="isFoodProvided"
                                        onChange={handleChange}
                                        checked={formData.isFoodProvided}
                                    />
                                    <label className='text_color2'> </label>
                                </li>

                            </ul>


                        </div>

                    </div>


                </div>
                : null

        );
    }

    const handleSector = () => {
        setSelectSector(!selectSector);
    }

    const showSector = () => {
        return (
            selectSector ?
            <Wrapper>
                <Selectors 
                    arrays={SECTORS} 
                    setter={setClickedSector}
                    setFormData={setFormData}
                    keyword='sector'
                />
                <BUTTON onClick={()=>{setSelectSector(!selectSector);}}></BUTTON>
            </Wrapper>
            : null
        );
    }

    const handlePeriod = () => {
        setSelectPeriod(!selectPeriod);
    }

    const showPeriod = () => {
        return (
            selectPeriod ?
                <Wrapper>
                    <Selectors
                        arrays={PERIODS}
                        setter={setClickedEmPeriod}
                        setFormData={setFormData}
                        keyword='employmentPeriod'
                    />
                <BUTTON onClick={()=>{setSelectPeriod(!selectPeriod);}}></BUTTON>
                </Wrapper>
                : null
        );
    }

    const handleStartHour = () => {
        setSelectStartHour(!selectStartHour);
    }
    const showStartHour = () => {
        return (
            selectStartHour ?
                <Wrapper>
                    <Selectors  
                        arrays={TIMES_HOUR}
                        setter={setClickedStartHour}
                        formData={createStartHour}
                        keyword='startHour' 
                    />
                    <BUTTON onClick={()=>{setSelectStartHour(!selectStartHour);}}></BUTTON>
                </Wrapper>
                : null
        );
    }

    const handleStartMin = () => {
        setSelectStartMin(!selectStartMin)
    }

    const showStartMin = () => {
        return (
            selectStartMin ?
                <Wrapper>
                    <Selectors
                        arrays={TIMES_MIN}
                        setter={setClickedStartMin}
                        formData={createStartMin}
                        keyword='startMin'
                    />
                   <BUTTON onClick={()=>{setSelectStartMin(!selectStartMin);}}></BUTTON>
                </Wrapper>
                : null
        );
    }

    const handleEndHour = () => {
        setSelectStartHour2(!selectStartHour2);
    }
    const showEndHour = () => {
        return (
            selectStartHour2 ?
                <Wrapper>
                    <Selectors  
                        arrays={TIMES_HOUR}
                        setter={setClickedStartHour2}
                        formData={createEndHour}
                        keyword='startHour2' 
                    />
                    <BUTTON onClick={()=>{setSelectStartHour(!selectStartHour);}}></BUTTON>
                </Wrapper>
                : null
        );
    }

    const handleEndMin = () => {
        setSelectStartMin2(!selectStartMin2)
    }


    const showEndMin = () => {
        return (
            selectStartMin2 ?
                <Wrapper>
                <Selectors
                        arrays={TIMES_MIN}
                        setter={setClickedStartMin2}
                        formData={createEndMin}
                        keyword='startMin2'
                    />
                <BUTTON onClick={()=>{setSelectStartMin2(!selectStartMin);}}></BUTTON>
                
                {/* <div className="pannel_bottom">
                    <button className="btn_reset"></button>
                    <button onClick={generateEmploymentPeriod} className="btn_apply"></button>
                </div> */}

                </Wrapper>
                : null
        );
    }

    const handleRegion = () => {
        setSelectRegion(!selectRegion)
    }
    const showRegion = () => {
        return (
            selectRegion ?
                <>
                    <div className="clicked_loc">
                        <div className="Loc_body">
                            <div className="item_list">
                                <ul className="centered_list">
                                    <label className="loc_checkbox custom-checkbox"><input type="checkbox" checked={clickedCity['']} onChange={() => dropChange('city', '')}></input><span></span></label>
                                    <label className="loc_checkbox custom-checkbox"><input type="checkbox" checked={clickedCity['']} onChange={() => dropChange('city', '')}></input><span></span></label>
                                    <label className="loc_checkbox custom-checkbox"><input type="checkbox" checked={clickedCity['']} onChange={() => dropChange('city', '')}></input><span></span></label>
                                    <label className="loc_checkbox custom-checkbox"><input type="checkbox" checked={clickedCity['']} onChange={() => dropChange('city', '')}></input><span></span></label>
                                    <label className="loc_checkbox custom-checkbox"><input type="checkbox" checked={clickedCity['']} onChange={() => dropChange('city', '')}></input><span></span></label>
                                    <label className="loc_checkbox custom-checkbox"><input type="checkbox" checked={clickedCity['']} onChange={() => dropChange('city', '')}></input><span></span></label>
                                    <label className="loc_checkbox custom-checkbox"><input type="checkbox" checked={clickedCity['']} onChange={() => dropChange('city', '')}></input><span></span></label>
                                    <label className="loc_checkbox custom-checkbox"><input type="checkbox" checked={clickedCity['']} onChange={() => dropChange('city', '')}></input><span></span></label>
                                    <label className="loc_checkbox custom-checkbox"><input type="checkbox" checked={clickedCity['']} onChange={() => dropChange('city', '')}></input><span></span></label>
                                    <label className="loc_checkbox custom-checkbox"><input type="checkbox" checked={clickedCity['']} onChange={() => dropChange('city', '')}></input><span></span></label>
                                    <label className="loc_checkbox custom-checkbox"><input type="checkbox" checked={clickedCity['']} onChange={() => dropChange('city', '')}></input><span></span></label>
                                    <label className="loc_checkbox custom-checkbox"><input type="checkbox" checked={clickedCity['']} onChange={() => dropChange('city', '')}></input><span></span></label>
                                    <label className="loc_checkbox custom-checkbox"><input type="checkbox" checked={clickedCity['']} onChange={() => dropChange('city', '')}></input><span></span></label>
                                    <label className="loc_checkbox custom-checkbox"><input type="checkbox" checked={clickedCity['']} onChange={() => dropChange('city', '')}></input><span></span></label>
                                    <label className="loc_checkbox custom-checkbox"><input type="checkbox" checked={clickedCity['']} onChange={() => dropChange('city', '')}></input><span></span></label>
                                    <label className="loc_checkbox custom-checkbox"><input type="checkbox" checked={clickedCity['']} onChange={() => dropChange('city', '')}></input><span></span></label>
                                    <label className="loc_checkbox custom-checkbox"><input type="checkbox" checked={clickedCity['']} onChange={() => dropChange('city', '')}></input><span></span></label>
                                    <label className="loc_checkbox custom-checkbox"><input type="checkbox" checked={clickedCity['']} onChange={() => dropChange('city', '')}></input><span></span></label>
                                    <label className="loc_checkbox custom-checkbox"><input type="checkbox" checked={clickedCity['']} onChange={() => dropChange('city', '')}></input><span></span></label>
                                    <label className="loc_checkbox custom-checkbox"><input type="checkbox" checked={clickedCity['']} onChange={() => dropChange('city', '')}></input><span></span></label>
                                    <label className="loc_checkbox custom-checkbox"><input type="checkbox" checked={clickedCity['']} onChange={() => dropChange('city', '')}></input><span></span></label>
                                    <label className="loc_checkbox custom-checkbox"><input type="checkbox" checked={clickedCity['']} onChange={() => dropChange('city', '')}></input><span></span></label>
                                    <label className="loc_checkbox custom-checkbox"><input type="checkbox" checked={clickedCity['']} onChange={() => dropChange('city', '')}></input><span></span></label>
                                    <label className="loc_checkbox custom-checkbox"><input type="checkbox" checked={clickedCity['']} onChange={() => dropChange('city', '')}></input><span></span></label>
                                    <label className="loc_checkbox custom-checkbox"><input type="checkbox" checked={clickedCity['']} onChange={() => dropChange('city', '')}></input><span></span></label>
                                    <label className="loc_checkbox custom-checkbox"><input type="checkbox" checked={clickedCity['']} onChange={() => dropChange('city', '')}></input><span></span></label>
                                    <label className="loc_checkbox custom-checkbox"><input type="checkbox" checked={clickedCity['']} onChange={() => dropChange('city', '')}></input><span></span></label>
                                    <label className="loc_checkbox custom-checkbox"><input type="checkbox" checked={clickedCity['']} onChange={() => dropChange('city', '')}></input><span></span></label>
                                </ul>

                            </div>
                        </div>

                    </div>
                </>
                : null
        );
    }



    return (
        <> <div className='center'>
            <h1 className='title'>  </h1>
            <form onSubmit={handleSubmit}>
                { /*   onChange   handleChange  . */}
                {/*    onChange  . */}

                <label className='job_title'></label><br />
                <input className="title_input" type="text" placeholder=' ' name="title" onChange={handleChange} value={formData.title} required /><br /><br />

                <div className='margin_bottom'>
                    <label className='job_title'></label><br />
                    <input type="text" className="title_input" placeholder='' name="companyName" onChange={handleChange} value={formData.companyName} required /><br />
                </div>
                <input type="text" className="title_input" placeholder='' name="companyName_en" onChange={handleChange} value={formData.companyName_en} required /><br /><br />
                <div className='margin_bottom'>
                    <label className='job_title'>  </label><br />
                    <input type="text" className="title_input" placeholder='  ()' name="detailAddress" onChange={handleChange} value={formData.detailAddress} required /><br></br>
                </div>
                <input type="text" className="title_input" placeholder='  ()' name="detailAddress_en" onChange={handleChange} value={formData.detailAddress_en} required /><br /><br />


                <div className='flex_container'>
                    <div className='wage_fix'>
                        <p className='job_title_small'></p>
                        <input type="number" placeholder="( )" className="wage_input" name="salary" onChange={handleChange} value={formData.salary} required /><br /><br />
                    </div>

                    <div className='sector_container'>
                        <div className="sector-header">
                            <label className='job_title_small'></label><br />
                            <button className='tap_style' onClick={handleSector}>{formData.sector===''?"":formData.sector}</button>
                        </div>
                        {showSector()}
                    </div>
                </div>

                <div className='flex_container'>
                    <div className='columm'>
                        <label className='job_title_small'></label>
                        <button className='tap_style' onClick={handleRegion}> </button>
                        {showRegion()}
                    </div>

                    <div className='column'>
                        <label className='job_title_small'>  </label> <br></br>
                        <button className='tap_style' onClick={handleFood} >  /   </button>
                    </div>
                </div>
                <div className='padding'>
                    {showFood()}
                </div>

                <div className='flex_container'>
                    <div>
                        <label className='job_title_small'> </label><br />
                        <button className='tap_style' onClick={handlePeriod}>{formData.employmentPeriod===''?"":formData.employmentPeriod}</button>
                        {showPeriod()}
                    </div>
                    <br></br>

                    <div className='work_fix'>
                        <label className='job_title_small'></label><br />
                        <input type="text" placeholder='' className="tap_style" name="workDays" onChange={handleChange} value={formData.workDays} required /><br /><br />
                    </div>
                </div>


                <div>
                    <div>
                        <p className='job_title_small2'>  </p>
                        <div className='flex_container'>
                            <div>
                                <button className='tap_style' onClick={handleStartHour}></button>
                                {showStartHour()}
                            </div>
                        
                            <div>
                                <button className='tap_style2' onClick={handleStartMin}>(10 )</button>  {/*     */}
                                {showStartMin()}
                            </div>
                        </div>
                    </div>               
                </div>

                <div>
                    <div>
                        <p className='job_title_small2'>  </p>
                        <div className='flex_container'>
                            <div>
                                <button className='tap_style' onClick={handleEndHour}></button>
                                {showEndHour()}
                            </div>
                            <div>
                                <button className='tap_style2' onClick={handleEndMin}>(10 )</button>
                                {showEndMin()}
                            </div>

                        </div>
      
                    </div>
                   
                </div>

                <label> </label><br />
                <input type="file" id="profileImage" name="profileImage" onChange={handleChange} multiple required /><br /><br />


                <label> </label><br />
                <input type="file" id="companyImages" name="companyImages" onChange={handleChange} multiple required /><br /><br />


                <label> </label><br />
                <input type="file" id="details" name="details" onChange={handleChange} multiple required /><br /><br />


                <button type="submit"></button>
                <button type="button" onClick={handlePrintFormData}>formData  </button>
            </form>
        </div>
        </>
    );
};

export default JobPostingForm;